<template>
  <div id="nav-icon3" :class="{open: opened}" @click="onClick">
    <span></span>
    <span></span>
    <span></span>
    <span></span>
  </div>
</template>

<script>
export default {
  props: ["opened"],
  data() {
    return {
      // opened: false
    };
  },
  methods: {
    onClick() {
      document.documentElement.classList.toggle("hide-scroll");
      document.body.classList.toggle("hide-scroll");
      this.$emit("close", !this.opened);
    }
  }
};
</script>

