<template>
  <v-app id="inspire">
    <!-- если на страницу login/registration -->
    <template v-if="loginPage">
      <router-view></router-view>
    </template>

    <!-- если на любой другой странице -->
    <template v-else>
      <main-header></main-header>
      <!-- если страница с сайдбарами -->
      <v-container v-if="pageWithSidebars">
        <v-layout row wrap justify-center>
          <v-flex md3 xl2 class="hidden-sm-and-down">
            <products-sidebar></products-sidebar>
            <!-- если страница с сайдбаром вервисов -->
            <services-sidebar v-if="pageWithServices"></services-sidebar>
          </v-flex>

          <router-view></router-view>
        </v-layout>
      </v-container>
      <!-- если страница без сайдбаров -->
      <v-container v-else>
        <v-layout row wrap justify-center>
          <v-flex xs12 xl9>
            <router-view></router-view>
          </v-flex>
        </v-layout>
      </v-container>

      <main-footer></main-footer>
    </template>
  </v-app>
</template>

<script>
import MainHeader from "./components/Main/MainHeader";
import MainFooter from "./components/Main/MainFooter";
import ProductsSidebar from "./components/Sidebars/ProductsSidebar";
import ServicesSidebar from "./components/Sidebars/ServicesSidebar";

export default {
  components: {
    MainHeader,
    MainFooter,
    ProductsSidebar,
    ServicesSidebar
  },
  data: () => ({
    drawer: true,
    info: []
  }),
  computed: {
    pageWithSidebars() {
      return this.$store.getters.pageWithSidebars;
    },
    pageWithServices() {
      return this.$store.getters.pageWithServices;
    },
    loginPage() {
      return this.$store.getters.loginPage;
    }
  },
  methods: {},
  created() {
    this.info = this.info.concat(this.$store.getters.products);
    // console.log(this.info);
    let productIdCount = 0;

    this.info.forEach(item => {
      item.link = item.phone.split(" ").join("-");
      item.categoryLinks = [];

      for (const category of item.categories) {
        item.categoryLinks.push(category.split(" ").join("-"));
      }

      for (const model of item.models) {
        productIdCount++;
        if (item.phone.includes("Iphone")) {
          model.size = "Gb";
        } else if (item.phone.includes("Watch")) {
          model.size = "mm";
        }
        model.category = item.link;
        model.id = productIdCount;
        this.$set(model, 'count', 1);
      }
    });
    this.$store.commit("setProducts", this.info);
  }
};
</script>

<style>
.hide-scroll {
  /* position:fixed; */
  top:0px;
  left:0px;
  overflow:hidden;
  height:100%;
}
</style>