<template>
  <!-- <div class="login-form"> -->
    <v-container class="login-form" fluid>
      <v-layout row wrap fill-height>
        <v-flex xs12 align-self-center>
          <h2 class="main-title">iPhoneService</h2>
          <v-form ref="form" class="contacts-form" v-model="validForm" @submit.prevent>
            <v-text-field
              class="textfield"
              v-model="email"
              :rules="emailRules"
              label="Ваш E-mail"
              required
              dark
            ></v-text-field>

            <v-text-field
              class="textfield"
              v-model="password"
              :counter="10"
              :rules="passwordRules"
              label="Ваш пароль"
              required
              dark
            ></v-text-field>

            <!-- <checkbox :checkbox="checkbox" @checkboxChanged="onCheckboxChanged"></checkbox> -->
            <div class="my-checkbox">
              <v-switch
                class="switch"
                v-model="confidentCheckbox"
                color="blue"
                hide-details
                required
                dark
              ></v-switch>
              <span class="confident">
                Вы согласны с
                <a href="#">политикой конфиденциальности</a>?
              </span>
            </div>

            <button
              class="to-cart"
              :disabled="btnDisabled"
              :class="{disabled: btnDisabled}"
              @click="onLogin"
            >{{btnText}}</button>
          </v-form>
        </v-flex>
      </v-layout>
    </v-container>
</template>

<script>
const emailRegex = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;

export default {
  props: ["page"],
  data() {
    return {
      validForm: false,
      password: "",
      email: "",
      message: "",
      confidentCheckbox: false,
      passwordRules: [
        v => !!v || "Поле Пароль должно быть заполнено",
        v => v.length <= 10 || "Пароль не должен превышать 10 символов"
      ],
      emailRules: [
        v => !!v || "Поле E-mail должно быть заполнено",
        v => emailRegex.test(v) || "E-mail должен быть верным"
      ]
    };
  },
  computed: {
    btnDisabled() {
      return !this.confidentCheckbox || !this.validForm;
    },
    btnText() {
      return this.page == "login" ? "Войти" : "Зарегистрировать";
    }
  },
  methods: {
    onCheckboxChanged(bool) {
      //для <checkbox></checkbox> (Checkbox.vue)
      this.confidentCheckbox = bool;
    },
    onLogin() {
      this.$router.push("/");
    }
  },
  beforeRouteLeave(to, from, next) {
    this.$store.commit("toFromLoginPage", false);

    next();
  },
  created() {
    // console.log(this.page);
  }
};
</script>

