<template>
  <div class="login-wrap">
    <div class="login">
      <v-icon class="face-icon">face</v-icon>
      <span class="cabinet hidden-md-and-down">Мой кабинет</span>
      <v-icon class="arrow-down">arrow_drop_down</v-icon>
      <div class="dropdown">
        <template v-if="!user">
          <p class="enter" @click="toLogRegPage($event)">
            <i class="fas fa-sign-in-alt"></i>
            <span>Войти</span>
          </p>
          <p class="reg" @click="toLogRegPage($event)">
            <i class="fas fa-user"></i>
            <span>Регистрация</span>
          </p>
        </template>
        <template v-else>
          <p class="toCab" @click="toLogRegPage($event)">
            <i class="fas fa-edit"></i>
            <span>В кабинет</span>
          </p>
          <p class="editProducts" v-if="user.isAdmin" @click="toLogRegPage($event)">
            <i class="fas fa-pencil-alt"></i>
            <span>Редактировать</span>
          </p>
          <p class="exit" @click="toLogRegPage($event)">
            <i class="fas fa-sign-out-alt"></i>
            <span>Выйти</span>
          </p>
        </template>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // user: {isAdmin: false},
      user: false,
    };
  },
  methods: {
    toLogRegPage(e) {
      if(e.target.classList.contains('enter') || e.target.parentElement.classList.contains('enter')) {
        this.$router.push('/login');
      }
      else if(e.target.classList.contains('reg') || e.target.parentElement.classList.contains('reg')) {
        this.$router.push('/registration');
      }
      else if(e.target.classList.contains('toCab') || e.target.parentElement.classList.contains('toCab')) {
        this.$router.push('/cabinet');
      }
      else if(e.target.classList.contains('editProducts') || e.target.parentElement.classList.contains('editProducts')) {
        this.$router.push('/admin-panel');
      }
      else if(e.target.classList.contains('exit') || e.target.parentElement.classList.contains('exit')) {
        this.logOut();
      }
      // this.$store.commit('toFromLoginPage', true);
    }
  }
};
</script>

