<template>
  <v-flex class="products" xs12 md9 xl7>
    <h2 class="main-title display-1">{{filteredProducts[0].phone}}</h2>
    <div class="podrazdeli-sort">
      <div class="podrazdeli">
        <span>Подразделы:</span>
        <router-link
          class="category-link"
          v-for="(category, j) in filteredProducts[0].categories"
          :key="j"
          :to="`/model/${filteredProducts[0].link}/category/${category.split(' ')[0]}-${category.split(' ')[1]}`"
        >{{category.split(' ').join('')}}</router-link>
      </div>
      <div class="sort">
        <span>Сортировка:</span>
        <div class="select-wrapper">
          <select v-model="select">
            <option>Цена</option>
            <option>Цвет</option>
          </select>
          <i class="fa fa-caret-down"></i>
        </div>
      </div>
    </div>

    <v-container grid-list-lg>
      <v-layout row wrap justify-center>
        <v-flex
          class="product-card-wrap"
          xs9 sm5 md4
          v-for="(item, i) in filteredProducts[0].filteredModels"
          :key="i"
        >
          <product-card
            :imageSrc="require(`@/assets/${item.imageSrc}`)"
            :name="item.name"
            :price="item.price"
            :category="item.category"
            :link="`/model/${item.category}/category/${item.memory}-${item.size}/product/${item.id}`"
            :productId="item.id"
          ></product-card>
        </v-flex>
      </v-layout>
    </v-container>
  </v-flex>
</template>

<script>
import ProductCard from "../components/Products/ProductCard";

import { mapGetters } from "vuex";

function sorting() {
  switch (this.select) {
    case "Цвет":
      this.filteredProducts[0].filteredModels.sort((a, b) => {
        if (a.color > b.color) return 1;
        if (a.color < b.color) return -1;
        // return a.color - b.color;
      });
      break;
    case "Цена":
      this.filteredProducts[0].filteredModels.sort((a, b) => {
        return a.price - b.price;
      });
      break;
  }
}

export default {
  props: ["id", "cat"],
  data() {
    return {
      filteredProducts: [],
      select: "Цена"
    };
  },
  components: {
    ProductCard
  },
  computed: {
    ...mapGetters({
      products: "products",
      filteredByModel: "filteredProducts"
    }),
    
  },
  methods: {
    
  },
  watch: {
    select() {
      sorting.call(this);
    }
  },
  beforeRouteUpdate(to, from, next) {

    let arr = this.filteredByModel(to.params.id, to.params.cat);

    if (this.filteredProducts.length == 0) {
      this.filteredProducts.push(arr);
    } else {
      this.filteredProducts.splice(0, 1, arr);
    }

    sorting.call(this);

    next();
  },
  created() {
    let arr = this.filteredByModel(this.id, this.cat);

    this.filteredProducts.push(arr);

    sorting.call(this);
  }
};
</script>

