<template>
  <transition name="slideRight">
    <div class="mobile-menu-wrap" @click="onClose($event)">
      <div class="mobile-menu">
        <div class="main-links">
          <v-btn
            v-for="(btn, i) in linkButtons"
            :key="i"
            :to="btn.link"
            flat
            icon
            :color="btn.color"
          >
            <v-icon>{{btn.icon}}</v-icon>
          </v-btn>
        </div>
        <div class="search-field">
          <v-text-field
            solo
            flat
            hide-details
            placeholder="Поиск по сайту..."
            append-icon="search"
            height="40"
            class="pl-3"
          ></v-text-field>
        </div>
        <!-- product links -->
        <router-link
          class="phone-list"
          tag="ul"
          v-for="(item,i) in info"
          :key="i"
          :to="`/model/${item.link}`"
        >
          <li class="phone-item" :class="{ active: item.isActive }">
            <div class="phone-header" @click="onActive(i)">{{item.phone}}</div>
            <div class="categories">
              <transition name="fadeHeight">
                <ul class="category-list" v-if="item.isActive">
                  <router-link
                    tag="li"
                    class="category-item"
                    v-for="(cat,j) in item.categories"
                    :key="j"
                    :to="`/model/${item.link}/category/${item.categoryLinks[j]}`"
                  >- {{cat}}</router-link>
                </ul>
              </transition>
            </div>
          </li>
        </router-link>
      </div>
    </div>
  </transition>
</template>

<script>
import { mapGetters } from "vuex";

function hasClass(element, cls) {
  return (' ' + element.className + ' ').indexOf(' ' + cls + ' ') > -1;
}

export default {
  props: ["mMenuOpened"],
  components: {},
  data() {
    return {
      isActive: false,
      info: [],
      linkButtons: [
        {
          icon: "home",
          color: "grey lighten-2",
          link: "/"
        },
        {
          icon: "security",
          color: "grey lighten-2",
          link: "/waranty"
        },
        {
          icon: "local_shipping",
          color: "grey lighten-2",
          link: "/dostavka"
        },
        {
          icon: "build",
          color: "grey lighten-2",
          link: "/remont"
        },
        {
          icon: "local_offer",
          color: "grey lighten-2",
          link: "/sale"
        },
        {
          icon: "phone_iphone",
          color: "grey lighten-2",
          link: "/contacts"
        }
      ]
    };
  },
  computed: {
    ...mapGetters({
      products: "products"
    })
  },
  methods: {
    onActive(num) {
      this.info.forEach(item => {
        item.isActive = false;
      });
      this.info[num].isActive = true;
    },
    toggleActiveByUrl() {
      if (this.$route.params.id) {
        for (const item of this.info) {
          item.isActive = false;

          if (item.phone.split(" ").join("-") == this.$route.params.id) {
            item.isActive = true;
          }
        }
      }
    },
    onClose(e) {
      if(!hasClass(e.target, 'mobile-menu-wrap')) return;
      this.$emit("close", false);
      document.documentElement.classList.toggle('hide-scroll');
      document.body.classList.toggle('hide-scroll');
    }
  },
  beforeRouteUpdate(to, from, next) {
    this.toggleActiveByUrl();
    next();
  },
  created() {
    // console.log(this.products);
    this.info = [...this.products];
    this.toggleActiveByUrl();
  }
};
</script>

