<template>
  <div class="stars">
    <i
      v-for="(star, i) in stars"
      :key="i"
      class="fa-star"
      :class="star.class"
      @click="onStarClick(i)"
    ></i>
  </div>
</template>

<script>
export default {
  data() {
    return {
      stars: [
        { class: "far" },
        { class: "far" },
        { class: "far" },
        { class: "far" },
        { class: "far" }
      ],
      rating: 3
    }
  },
  methods: {
    onStarClick(num) {
      this.stars[num].class = "fas";
      this.rating = num + 1;
      for (let i = 0; i < this.stars.length; i++) {
        if (i < num) {
          this.stars[i].class = "fas";
        } else if (i > num) {
          this.stars[i].class = "far";
        }
      }
    }
  },
  created() {
    for (let i = 0; i < this.rating; i++) {
      this.stars[i].class = "fas";
    }
  }
};
</script>

<style>
.stars i {
  margin-right: 10px;
  font-size: 16px;
  color: #757575;
}
.stars i:hover {
  color: #9e9d9d;
}
</style>
