<template>
  <div class="card">
    <router-link tag="div" class="top-wrap" :to="link">
      <div class="image-wrap">
        <img :src="imageSrc" alt>
      </div>
      <div class="text-wrap">
        <h3>{{name}}</h3>
        <p>{{price}} руб.</p>
      </div>
    </router-link>

    <div class="buttons">
      <button class="to-cart" :class="{disabled: cartBtnDisabled}" :disabled="cartBtnDisabled" @click="addToCart">Добавить в корзину</button>
      <button class="one-click" @click="oneClickBuy">Купить в один клик</button>
    </div>

    <add-to-cart-alert v-if="addToCartAlert"></add-to-cart-alert>
    <one-click v-if="oneClickPopup" @close="oneClickPopupClose"></one-click>
  </div>
</template>

<script>
import AddToCartAlert from '@/components/Popups/AddToCartAlert'
import OneClick from '@/components/Popups/OneClick'
export default {
  props: ['imageSrc', 'name', 'category', 'price', 'link', 'productId'],
  components: {
    AddToCartAlert,
    OneClick
  },
  data() {
    return {
      title: '',
      cartBtnDisabled: false,
      addToCartAlert: false,
      oneClickPopup: false
    }
  }, 
  methods: {
    addToCart() {
      this.cartBtnDisabled = true;
      let product = this.$store.getters.filteredByProductId(this.category, this.productId);
      let productInCart = this.$store.getters.getCart.find(item => {
        return item.id == product.id;
      })
      
      setTimeout(() => {
        this.cartBtnDisabled = false;
        this.addToCartAlert = true;
        if(productInCart) {
          product.count++;
        } else {
          product.count = 1;
          this.$store.commit('addToCart', product);
        }

        setTimeout(() => {
          this.addToCartAlert = false;
        }, 1500);
      }, 1500);
    },
    oneClickBuy() {
      this.oneClickPopup = true;
    },
    oneClickPopupClose() {
      this.oneClickPopup = false;
    }
  }
};
</script>

