import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Model from './views/Model.vue'
import Product from './views/Product.vue'
import Contacts from './views/Contacts.vue'
import LoginPage from './views/LoginPage.vue'
import CabinetPage from './views/Cabinet.vue'
import CartPage from './views/CartPage.vue'

import Waranty from './views/notMainPages/Waranty'
import Dostavka from './views/notMainPages/Dostavka'
import Remont from './views/notMainPages/Remont'
import Sale from './views/notMainPages/Sale'

import store from './store'

Vue.use(Router)

function hideServices (to, from, next) {
  store.commit('changeServicesState', false);
  next();
}

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/model/:id',
      name: 'model',
      props: true,
      component: Model,
      children: [
        {
          path: 'category/:cat',
          props: true,
          component: Model
        }
      ]
    },
    {
      path: '/model/:id/category/:cat/product/:productId',
      name: 'product',
      props: true,
      component: Product
    },
    {
      path: '/contacts',
      name: 'contacts',
      component: Contacts,
      beforeEnter: (to, from, next) => {
        store.commit('changePage', false);
        next();
      }
    },
    {
      path: '/login',
      name: 'login',
      props: { page: 'login' },
      component: LoginPage,
      beforeEnter: (to, from, next) => {
        store.commit('toFromLoginPage', true);
        next();
      }
    },
    {
      path: '/registration',
      name: 'registration',
      props: { page: 'reg' },
      component: LoginPage,
      beforeEnter: (to, from, next) => {
        store.commit('toFromLoginPage', true);
        next();
      }
    },
    {
      path: '/cabinet',
      name: 'cabinet',
      component: CabinetPage,
      beforeEnter: (to, from, next) => {
        store.commit('changePage', false);
        next();
      }
    },
    {
      path: '/cart',
      name: 'cart',
      component: CartPage,
      beforeEnter: (to, from, next) => {
        store.commit('changePage', false);
        next();
      }
    },
    // not main pages
    {
      path: '/waranty',
      name: 'waranty',
      component: Waranty,
      beforeEnter: hideServices
    },
    {
      path: '/dostavka',
      name: 'dostavka',
      component: Dostavka,
      beforeEnter: hideServices
    },
    {
      path: '/remont',
      name: 'remont',
      component: Remont,
      beforeEnter: hideServices
    },
    {
      path: '/sale',
      name: 'sale',
      component: Sale,
      beforeEnter: hideServices
    }
  ]
})
