<template>
  <div class="sidebar">
    <router-link class="phone-list" tag="ul" v-for="(item,i) in info" :key="i" :to="`/model/${item.link}`">
        <li class="phone-item" :class="{ active: item.isActive }">
          <div class="phone-header" @click="onActive(i)">{{item.phone}}</div>
          <div class="categories">
            <transition name="fadeHeight">
              <ul class="category-list" v-if="item.isActive">
                <router-link tag="li" class="category-item" v-for="(cat,j) in item.categories" :key="j" :to="`/model/${item.link}/category/${item.categoryLinks[j]}`">- {{cat}}</router-link>
              </ul>
            </transition>
          </div>
          </li>
      </router-link>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  components: {},
  data() {
    return {
      isActive: false,
      info: []
    };
  },
  computed: {
    ...mapGetters({
      products: 'products'
    })
  },
  methods: {
    onActive(num) {
      this.info.forEach((item) => {
        item.isActive = false;
      });
      this.info[num].isActive = true;
    },
    toggleActiveByUrl() {
      if(this.$route.params.id) {
        for (const item of this.info) {
          item.isActive = false;

          if(item.phone.split(' ').join('-') == this.$route.params.id) {
            item.isActive = true;
          }
        }
      }
    }
  },
  beforeRouteUpdate(to, from, next) {
    this.toggleActiveByUrl();
    next();
  },
  created() {
    // console.log(this.products);
    this.info = [...this.products];
    this.toggleActiveByUrl();
  }
};
</script>

