<template>
  <div class="cart-page">
    <div class="empty-cart" v-if="cart.length == 0">
      <p class="empty-text grey--text text--darken-3">В Вашей корзине пока ничего нет</p>
    </div>
    <div class="whole-cart" v-else>
      <h2 class="main-title display-1 grey--text text--darken-3">Корзина</h2>
      <v-layout row wrap justify-center>
        <!-- список товаров в корзине -->
        <v-flex xs12 sm9 md8>
          <div class="cart-products-list">
            <div class="cart-product" v-for="(product, i) in cart" :key="i">
              <v-layout row wrap>
                <v-flex sm3 xs6>
                  <div class="img-wrap">
                    <img :src="require(`@/assets/${product.imageSrc}`)" alt>
                  </div>
                </v-flex>
                <v-flex sm4 xs6>
                  <div class="options-wrap">
                    <router-link
                      :to="`/model/${product.category}/category/${product.memory}-${product.size}/product/${product.id}`"
                    >{{product.name}}</router-link>
                    <p class="option grey--text text--darken-3">
                      <span>Цвет:</span>
                      {{product.color}}
                    </p>

                    <div class="plus-minus">
                      <v-btn
                        class="plus-btn"
                        flat
                        small
                        icon
                        color="indigo"
                        @click="onCount(product, 'plus')"
                      >
                        <v-icon>add</v-icon>
                      </v-btn>
                      <span class="product-count">{{product.count}}</span>
                      <v-btn
                        class="minus-btn"
                        flat
                        small
                        icon
                        color="indigo"
                        @click="onCount(product, 'minus')"
                      >
                        <v-icon>remove</v-icon>
                      </v-btn>
                    </div>
                  </div>
                </v-flex>
                <v-flex sm3 xs6 align-self-center>
                  <div class="price-wrap">
                    <p class="headline grey--text text--darken-3">
                      {{product.price}}
                      <span>руб./шт.</span>
                    </p>
                  </div>
                </v-flex>
                <v-flex sm2 xs6 align-self-end>
                  <div class="close-wrap">
                    <div class="close" @click="deleteProduct(i)">
                      <v-icon>close</v-icon>
                      <span>Удалить</span>
                    </div>
                  </div>
                </v-flex>
              </v-layout>
            </div>
          </div>
        </v-flex>
        <!-- total, go to order -->
        <v-flex sm6 md4>
          <div class="make-order">
            <div class="total-count">
              <span>Количество:</span>
              <span>{{productsCount}} шт.</span>
            </div>
            <div class="total-price">
              <span>Итого:</span>
              <span>{{price}} руб.</span>
            </div>
            <button class="to-order">Перейти к оформлению</button>
          </div>
        </v-flex>
      </v-layout>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  computed: {
    cart() {
      return this.$store.getters.getCart;
    },
    productsCount() {
      return this.$store.getters.productsCount;
    },
    price() {
      return this.$store.getters.totalPrice;
    }
  },
  methods: {
    onCount(product, count) {
      count == "plus" ? product.count++ : product.count--;
      if (product.count < 1) product.count = 1;
    },
    deleteProduct(num) {
      this.$store.commit("removeFromCart", num);
    },
    clearCart() {
      this.$store.commit("clearCart");
    }
  },
  beforeRouteLeave(to, from, next) {
    this.$store.commit("changePage", true);
    next();
  }
};
</script>

