export default {
  state: {
    cart: []
  },
  getters: {
    getCart(state) {
      return state.cart;
    },
    totalPrice(getters, rootGetters) {
      let cart = rootGetters.getCart;
      if (cart.length != 0) {
        let totalPrice = 0;
        cart.forEach(item => {
          totalPrice += (item.price * item.count);
        });
        return totalPrice;
      } else {
        return 0;
      }
    },
    productsCount(getters, rootGetters) {
      let cart = rootGetters.getCart;
      if(cart.length != 0) {
        let totalCount = 0;
        cart.forEach(item => {
            totalCount += +item.count;
        });
        return totalCount;
      } else {
        return 0;
      }
    },
  },
  mutations: {
    addToCart(state, payload) {
      if(Array.isArray(payload)) {
        state.cart.push(...payload);
      } else {
        state.cart.push(payload);
      }
    },
    removeFromCart(state, payload) {
      state.cart.splice(payload, 1);
    },
    clearCart(state) {
      state.cart = [];
    }
  },
  actions: {}
}