<template>
  <v-flex class="product" xs12 md9 xl7>
    <v-layout row wrap justify-center>
      <v-flex class="info-row" xs10 sm7>
        <h2 class="main-title grey--text text--darken-3">{{product.name}}</h2>
        <div class="model">
          <span class="grey--text text--darken-1">Модель:</span>
          <span class="weight">{{product.phone}}</span>
        </div>
        <div class="exists">
          <span class="grey--text text--darken-1">Наличие:</span>
          <span class="weight">Есть в наличии</span>
        </div>
        <div class="price">
          <span>{{product.price}} руб.</span>
        </div>

        <div class="add-to-cart">
          <button class="to-cart" :class="{disabled: cartBtnDisabled}" :disabled="cartBtnDisabled" @click="addToCart">Добавить в корзину</button>
          <div class="input-value">
            <span>Количество</span>
            <input type="text" v-model="productCount">
          </div>
        </div>

        <div class="rating">
          <stars-rating></stars-rating>

          <div class="otzyvy">
            <a href="#" class="otzyv">{{0}} отзывов</a>
            <span>/</span>
            <a href="#" class="write-otzyv">Написать отзыв</a>
          </div>
        </div>
      </v-flex>
      <v-flex class="img-row" xs10 sm5>
        <div class="img-wrap">
          <img :src="require(`@/assets/${product.imageSrc}`)" alt>
        </div>
      </v-flex>

      <product-tabs 
      :characteristics="product.characteristics"
      :description="product.description"
      ></product-tabs>
    </v-layout>

    <div class="recommended-products">
      <h2 class="main-title headline grey--text text--darken-3">Рекомендуем</h2>
      <v-container grid-list-lg>
        <v-layout row wrap justify-center>
          <v-flex class="product-card-wrap" xs9 sm5 md4 v-for="(item, i) in recommended" :key="i">
            <product-card
              :imageSrc="require(`@/assets/${item.imageSrc}`)"
              :name="item.name"
              :price="item.price"
              :link="`/model/${item.category}/category/${item.memory}-${item.size}`"
            ></product-card>
          </v-flex>
        </v-layout>
      </v-container>
    </div>

    <add-to-cart-alert v-if="addToCartAlert"></add-to-cart-alert>

  </v-flex>
</template>

<script>
import ProductTabs from "../components/Products/ProductTabs";
import StarsRating from "../components/Products/StarsRating";
import ProductCard from "../components/Products/ProductCard";
import AddToCartAlert from '@/components/Popups/AddToCartAlert'

export default {
  props: ['id', 'productId'],
  components: {
    ProductTabs,
    StarsRating,
    ProductCard,
    AddToCartAlert
  },
  data() {
    return {
      productCount: 1,
      cartBtnDisabled: false,
      addToCartAlert: false,
      recommended: [
        {
          name: "iPhone 6 Plus 64Gb Space Gray (MKQN2RU/A)",
          memory: "64",
          color: "Space Gray",
          price: 49990,
          size: 'Gb',
          category: "Apple-Iphone-6-Plus",
          imageSrc: "images/products/iPhone6Plus/iphone6p-gray.jpg"
        },
        {
          name: "iPhone 5s 16Gb Gold A1457 (ME434RU/A)",
          memory: "16",
          color: "Gold",
          price: 30790,
          size: 'Gb',
          category: "Apple-Iphone-5s",
          imageSrc: "images/products/iPhone5S/iphone5s-gold.jpg"
        },
        {
          name: "IPhone 7 Plus 128Gb Black (MKU32RU/A)",
          memory: "128",
          color: "Black",
          price: 76990,
          size: 'Gb',
          category: "Apple-Iphone-7-Plus",
          imageSrc: "images/products/iPhone7Plus/iphone7-plus-black.jpg"
        }
      ]
    };
  },
  computed: {
    product() {
      return this.$store.getters.filteredByProductId(this.id, this.productId);
    }
  },
  methods: {
    addToCart() {
      this.cartBtnDisabled = true;
      let product = this.$store.getters.filteredByProductId(this.id, this.productId);
      let productInCart = this.$store.getters.getCart.find(item => {
        return item.id == product.id;
      })

      setTimeout(() => {
        this.cartBtnDisabled = false;
        this.addToCartAlert = true;
        if(productInCart) {
          product.count = +product.count + +this.productCount;
        } else {
          product.count = +this.productCount;
          this.$store.commit('addToCart', product);
        }

        setTimeout(() => {
          this.addToCartAlert = false;
        }, 1500);
      }, 1500);
    }
  },
  created() {
    // console.log(this.product);
  }
};
</script>

