<template>
  <v-form ref="form" class="contacts-form" v-model="validForm">
    <v-text-field v-model="name" :counter="10" :rules="nameRules" label="Ваше имя" required></v-text-field>

    <v-text-field v-model="email" :rules="emailRules" label="Ваш E-mail" required></v-text-field>

    <v-textarea name="message-input" label="Введите сообщение" v-model="message"></v-textarea>

    <!-- <checkbox :checkbox="checkbox" @checkboxChanged="onCheckboxChanged"></checkbox> -->
    <div class="my-checkbox">
      <v-switch class="switch" v-model="confidentCheckbox" hide-details required></v-switch>
      <span class="confident">Вы согласны с <a href="#">политикой конфиденциальности</a>?</span>
    </div>

    <button class="to-cart" :disabled="btnDisabled" :class="{disabled: btnDisabled}">Отправить сообщение</button>
  </v-form>
</template>

<script>
// import Checkbox from "@/components/Main/Checkbox";

const emailRegex = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;

export default {
  components: {
    // Checkbox
  },
  data() {
    return {
      validForm: false,
      name: "",
      email: "",
      message: "",
      confidentCheckbox: false,
      nameRules: [
        v => !!v || 'Поле Имя должно быть заполнено',
        v => v.length <= 10 || 'Name must be less than 10 characters'
      ],
      emailRules: [
        v => !!v || 'Поле E-mail должно быть заполнено',
        v => emailRegex.test(v) || 'E-mail должен быть верным'
      ]
    };
  },
  computed: {
    btnDisabled() {
      return !this.confidentCheckbox || !this.validForm
    }
  },
  methods: {
    onCheckboxChanged(bool) { //для <checkbox></checkbox> (Checkbox.vue)
      this.confidentCheckbox = bool;
    }
  }
};
</script>

