export default {
  state: {
    pageWithSidebars: true,
    pageWithServices: true,
    loginPage: false,
    bodyCantScroll: false
  },
  getters: {
    pageWithSidebars(state) {
      return state.pageWithSidebars;
    },
    pageWithServices(state) {
      return state.pageWithServices;
    },
    loginPage(state) {
      return state.loginPage;
    }
  },
  mutations: {
    changePage(state, payload) {
      state.pageWithSidebars = payload;
    },
    changeServicesState(state, payload) {
      state.pageWithServices = payload;
    },
    toFromLoginPage(state, payload) {
      state.loginPage = payload;
    }
  },
  actions: {}
}