<template>
  <div class="callback-wrap" @click="onBgClick($event)">
    <div class="callback">
      <h4 class="callback-title headline">Заказать звонок</h4>
      <i class="close-icon fas fa-times" @click="onClose"></i>
      <v-form ref="form" class="contacts-form" v-model="validForm" dark>
        <v-text-field v-model="name" :counter="10" :rules="nameRules" label="Ваше имя" required dark></v-text-field>

        <v-text-field v-model="phone" :rules="phoneRules" label="Ваш телефон" required dark></v-text-field>

        <!-- <checkbox :checkbox="checkbox" @checkboxChanged="onCheckboxChanged"></checkbox> -->
        <div class="my-checkbox">
          <v-switch class="switch" color="blue" v-model="confidentCheckbox" hide-details required dark></v-switch>
          <span class="confident">Вы согласны с
            <a href="#">политикой конфиденциальности</a>?
          </span>
        </div>

        <button
          class="to-cart"
          :disabled="btnDisabled"
          :class="{disabled: btnDisabled}"
        >Отправить сообщение</button>
      </v-form>
    </div>
  </div>
</template>

<script>
function hasClass(element, cls) {
  return (' ' + element.className + ' ').indexOf(' ' + cls + ' ') > -1;
}
export default {
  props: ["callbackVisible"],
  data() {
    return {
      validForm: false,
      name: "",
      phone: "",
      confidentCheckbox: false,
      nameRules: [
        v => !!v || 'Поле "Имя" должно быть заполнено',
        v => v.length <= 10 || 'Name must be less than 10 characters'
      ],
      phoneRules: [
        v => !!v || 'Поле "Телефон" должно быть заполнено'
      ]
    };
  },
  computed: {
    btnDisabled() {
      return !this.confidentCheckbox || !this.validForm
    }
  },
  methods: {
    onBgClick(e) {
      if(hasClass(e.target, 'callback-wrap')) {
        this.$emit("close", !this.callbackVisible);
      }
    },
    onClose() {
      this.$emit("close", !this.callbackVisible);
    }
  }
};
</script>

