<template>
  <div class="cabinet-wrap">
    <v-layout row wrap justify-center>
      <v-flex xs12 md4>
        <div class="img-wrap">
          <img :src="imageSrc" alt>
          <v-btn class="blue elevation-0" @click="triggerUpload">Загрузить
            <v-icon right>cloud_upload</v-icon>
          </v-btn>
          <input
            ref="fileInput"
            type="file"
            style="display: none;"
            accept="image/*"
            @change="onFileChange"
          >
        </div>
      </v-flex>
      <v-flex xs12 sm9 md8>
        <v-form ref="form" class="contacts-form" v-model="validGeneralForm" @submit.prevent>
          <div class="info-wrap">
            <div class="name-info">
              <h3 class="headline grey--text text--darken-3">Общая информация:</h3>
              <v-text-field v-model="firstName" :rules="firstNameRules" label="Ваше имя" required></v-text-field>
              <v-text-field v-model="surName" :rules="surNameRules" label="Ваша фамилия" required></v-text-field>
              <v-text-field v-model="dadName" :rules="dadNameRules" label="Ваше отчество" required></v-text-field>
            </div>
          </div>
        </v-form>
        <v-form ref="form2" class="contacts-form" v-model="validPasswordForm" @submit.prevent>
          <div class="password-info">
            <h3 class="headline grey--text text--darken-3">Пароль:</h3>
            <v-text-field type="password" v-model="password" :rules="passwordRules" label="Старый пароль" required></v-text-field>
            <v-text-field type="password" v-model="newPassword" :rules="newPasswordRules" label="Новый пароль" required
            ></v-text-field>
            <v-text-field type="password" v-model="confirmPassword" :rules="confirmPasswordRules" label="Подтвердите пароль"
              required
            ></v-text-field>
          </div>
        </v-form>
        <button
          class="save"
          :disabled="btnDisabled"
          :class="{disabled: btnDisabled}"
          @click="onSave"
        >Сохранить</button>
      </v-flex>
      <v-snackbar
        v-model="snackbar"
        :top="true"
        :color="snackbarObj.color"
        :timeout="3000"
      >
        {{snackbarObj.message}}
        <v-btn dark flat @click="snackbar = false">Закрыть</v-btn>
      </v-snackbar>
    </v-layout>
  </div>
</template>

<script>
export default {
  data() {
    return {
      snackbar: false,
      snackbarObj: {
        color: 'success',
        message: ''
      },
      user: {
        firstName: '',
        surName: '',
        dadName: ''
      },
      validGeneralForm: false,
      validPasswordForm: false,
      imageSrc: 'images/noavatar.jpeg',
      changedImageSrc: '',
      firstName: "",
      surName: "",
      dadName: "",
      password: "",
      newPassword: "",
      confirmPassword: "",
      loadedImage: null,
      firstNameRules: [v => !!v || "Поле 'Имя' должно быть заполнено"],
      surNameRules: [v => !!v || "Поле 'Фамилия' должно быть заполнено"],
      dadNameRules: [v => !!v || "Поле 'Отчество' должно быть заполнено"],
      passwordRules: [v => !!v || "Поле 'Старый пароль' должно быть заполнено"],
      newPasswordRules: [v => !!v || "Поле 'Новый пароль' должно быть заполнено"],
      confirmPasswordRules: [
        v => !!v || "Поле 'Подтвердите пароль' должно быть заполнено",
        v => v == this.newPassword || "Пароли должны совпадать"
      ]
    };
  },
  computed: {
    btnDisabled() {
      if(this.changedImageSrc !== "") {
        if (((this.emptyGeneralFields && !this.validGeneralForm) && (this.emptyPasswordFields && !this.validPasswordForm)) || 
            ((this.sameGeneralFields && this.validGeneralForm) && (this.emptyPasswordFields && !this.validPasswordForm)) ||
            ((this.emptyGeneralFields && !this.validGeneralForm) && (!this.emptyPasswordFields && this.validPasswordForm)) || 
            ((this.sameGeneralFields && this.validGeneralForm) && (!this.emptyPasswordFields && this.validPasswordForm)) || 
            ((!this.emptyGeneralFields && !this.sameGeneralFields && this.validGeneralForm) && (this.emptyPasswordFields && !this.validPasswordForm)) || ((!this.emptyGeneralFields && !this.sameGeneralFields && this.validGeneralForm) && (!this.emptyPasswordFields && this.validPasswordForm))) {
          return false;
        }
        return true;
      } else {
        if((!this.emptyGeneralFields && !this.sameGeneralFields && this.validGeneralForm) && (this.emptyPasswordFields && !this.validPasswordForm)) {
          return false;
        } else if((!this.emptyGeneralFields && !this.sameGeneralFields && this.validGeneralForm) && (!this.emptyPasswordFields && this.validPasswordForm)) {
          return false;
        } else if((this.emptyGeneralFields && !this.sameGeneralFields && !this.validGeneralForm) && (!this.emptyPasswordFields && this.validPasswordForm)) {
          return false;
        } else if((!this.emptyGeneralFields && this.sameGeneralFields && this.validGeneralForm) && (!this.emptyPasswordFields && this.validPasswordForm)) {
          return false;
        }
        return true
      }
    },
    emptyGeneralFields() {
      return this.firstName.length == 0 && this.surName.length == 0 && this.dadName.length == 0;
    },
    sameGeneralFields() {
      if(this.user) {
        return (this.firstName == this.user.firstName && this.user.firstName !== '') && (this.surName == this.user.surName && this.user.surName !== '') && (this.dadName == this.user.dadName && this.user.dadName !== '');
      }
      return false;
    },
    emptyPasswordFields() {
      return this.password.length == 0 && this.newPassword.length == 0 && this.confirmPassword.length == 0;
    }
  },
  methods: {
    triggerUpload() {
      // при нажатии на кнопку нажимаем на инпут
      this.$refs.fileInput.click();
    },
    onFileChange(event) {
      const file = event.target.files[0];
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.imageSrc = reader.result;
        this.changedImageSrc = reader.result;
      };
      // this.image = file;
    },
    onSave() {
      console.log('onSave');
    },
    saveInfo(payload) {
      console.log('saveInfo');
    },
    saveImage() {
      console.log('saveImage');
    }
  },
  beforeRouteLeave(to, from, next) {
    // вызывается перед переходом от пути, соответствующего текущему компоненту;
    // имеет доступ к контексту экземпляра компонента `this`.
    this.$store.commit("changePage", true);

    next();
  }
};
</script>

