  margin-bottom: 20px;
}
.cabinet-wrap .img-wrap button {
  color: #fff;
  margin: 0;
  border-radius: 0;
}

.contacts-form h3 {
  margin-bottom: 15px;
}

.name-info, .password-info {
  margin-bottom: 30px;
}

.cabinet-wrap .save {
  /* margin-top: 30px; */
  padding: 12px 20px;
  background-color: #2196f3;
  min-width: 200px;
