<template>
  <div class="cart-dropdown-wrap">
    <div class="cart-dropdown" v-if="cart.length !== 0">
      <div class="mini-product-card" v-for="(product, i) in cart" :key="i">
        <router-link
          tag="div"
          class="img-wrap"
          :to="`/model/${product.category}/category/${product.memory}-${product.size}/product/${product.id}`"
        >
          <img :src="require(`@/assets/${product.imageSrc}`)" alt>
        </router-link>
        <div class="text-wrap">
          <router-link
            :to="`/model/${product.category}/category/${product.memory}-${product.size}/product/${product.id}`"
          >{{product.name}}</router-link>
          <p class="option grey--text text--darken-3"><span>Цена:</span> {{product.price}}</p>
          <div class="plus-minus">
            <v-btn class="plus-btn" flat small icon color="indigo" @click="onCount(product, 'plus')">
              <v-icon>add</v-icon>
            </v-btn>
            <span class="product-count">{{product.count}}</span>
            <v-btn
              class="minus-btn"
              flat
              small
              icon
              color="indigo"
              @click="onCount(product, 'minus')"
            >
              <v-icon>remove</v-icon>
            </v-btn>
          </div>
        </div>
        <div class="close-wrap" @click="deleteProduct(i)">
          <i class="fas fa-times-circle"></i>
        </div>
      </div>
      <div class="buttons-wrap">
        <router-link tag="button" class="btn to-cart" to="/cart">Перейти в корзину</router-link>
        <button class="btn clear-cart" @click="clearCart">Очистить корзину</button>
      </div>
    </div>
    <div class="cart-dropdown" v-else>
      <div class="empty-cart">
        <span>Корзина пуста</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["cart"],
  methods: {
    onCount(product, count) {
      count == "plus" ? product.count++ : product.count--;
      if (product.count < 1) product.count = 1;
    },
    deleteProduct(num) {
      this.$store.commit("removeFromCart", num);
    },
    clearCart() {
      this.$store.commit("clearCart");
    }
  }
};
</script>

